<template>
    <div class="white-card px-4 py-4 add-edit-page">
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'carriers' }"
            >
                Carriers /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
        <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Name" label-for="inputName" class="required">
                    <b-form-input
                        v-model="$v.form.name.$model"
                        id="inputName"
                        label="Name"
                        v-bind:class="{ 'is-invalid': $v.form.name.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Account number" label-for="inputAccountNumber">
                    <b-form-input
                        id="inputAccountNumber"
                        v-model="$v.form.account_number.$model"
                        v-bind:class="{ 'is-invalid': $v.form.account_number.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Phone number" label-for="inputPhoneNumber">
                    <b-form-input
                        id="inputPhoneNumber"
                        v-model="$v.form.phone_number.$model"
                        v-bind:class="{ 'is-invalid': $v.form.phone_number.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        Phone number must contain 10 digits
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Email address" label-for="inputEmailAddress">
                    <b-form-input
                        id="inputEmailAddress"
                        v-model="$v.form.email_address.$model"
                        v-bind:class="{ 'is-invalid': $v.form.email_address.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        Enter a valid email address
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="URL" label-for="inputUrl">
                    <b-form-input
                        id="inputUrl"
                        v-model="$v.form.url.$model"
                        v-bind:class="{ 'is-invalid': $v.form.url.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        Enter a valid URL
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
                <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
            </div>
        </b-form>
    </div>
</template>

<script>
import { required, email, url, numeric, minLength, maxLength } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
const backend = new Backend();

export default {
    name: 'CarriersAddEditPage',
    components: {
        Preloader
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id'
    ],
    validations: {
        form: {
            name: {
                required
            },
            account_number: {

            },
            phone_number: {
                numeric,
                minLength: minLength(10),
                maxLength: maxLength(10)
            },
            email_address: {
                email
            },
            url: {
                url
            }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Carriers' : 'Add Carriers';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/carriers/' + id)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = this.form;
            this.isSaving = true;
            if (this._props.pageType === 'add') {
                backend.postRequest('services/carriers/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.errorMessage = null;
                        this.successMessage = `${name} was added successfully`;
                        this.isSaving = false;
                        this.clearForm();
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/carriers/' + this.$route.params.id + '/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `${name} was changed successfully`;
                    })
                    .catch(this.onSaveError);
            }
        },
        onSaveError(error) {
            this.isSaving = false;
            this.successMessage = null;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        onFetchingError(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        clearForm() {
            this.form = {
                name: '',
                account_number: '',
                phone_number: '',
                email_address: '',
                url: ''
            };
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
