<template>
    <div :class="!isModal ? 'white-card px-4 py-4 add-edit-page' : 'px-2'">
        <div v-if="!isModal">
            <div class="page-breadcrumb">
                <router-link
                    class=""
                    :to="{ name: 'devices' }"
                >
                    Devices /
                </router-link>
            </div>
            <h3 class="page-title">{{ title }}</h3>
        </div>
        <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Number" label-for="inputNumber">
                    <b-form-input
                        v-model="$v.form.number.$model"
                        id="inputNumber"
                        type="number"
                        v-bind:class="{ 'is-invalid': $v.form.number.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field must contain maximum 6 digits
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Location" label-for="inputLocation" class="required">
                    <b-form-select :options="locations"
                                   id="inputLocation"
                                   v-model="$v.form.location.$model"
                                   :class="{ 'is-invalid': $v.form.location.$error }"
                    ></b-form-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Support Contract" label-for="inputSupportContract">
                    <b-form-select :options="supportContracts"
                                   id="inputSupportContract"
                                   v-model="$v.form.support_contract.$model"
                                   :class="{ 'is-invalid': $v.form.support_contract.$error }"
                    ></b-form-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group
                    v-if="deviceModels && deviceModels.length"
                    label="Device Model"
                    label-for="inputDeviceModel"
                    class="mb-0 required"
                >
                    <b-form-select
                        v-model="$v.form.device_model.$model"
                        id="inputDeviceModel"
                        :options="deviceModels"
                        class="mb-3"
                        v-bind:class="{ 'is-invalid': $v.form.device_model.$error }"/>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Serial number" label-for="inputSerialNumber" class="required">
                    <b-form-input
                        v-model="$v.form.serial_number.$model"
                        id="inputSerialNumber"
                        v-bind:class="{ 'is-invalid': $v.form.serial_number.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Host name" label-for="inputHostName">
                    <b-form-input
                        v-model="$v.form.hostname.$model"
                        id="inputHostName"
                        v-bind:class="{ 'is-invalid': $v.form.hostname.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Management IP" label-for="inputManagementIp">
                    <b-form-input
                        v-model="$v.form.management_ip.$model"
                        id="inputManagementIp"
                        v-bind:class="{ 'is-invalid': $v.form.management_ip.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        Enter correct IP address
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Contract Start" label-for="inputContractStartDate">
                    <date-picker
                        id="inputContractStartDate"
                        v-model="$v.form.contract_start_date.$model"
                        lang="en"
                        format="YYYY-MM-DD"
                    ></date-picker>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Contract End" label-for="inputContractEndDate">
                    <date-picker
                        id="inputContractEndDate"
                        v-model="$v.form.contract_end_date.$model"
                        lang="en"
                        format="YYYY-MM-DD"
                    ></date-picker>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
                <b-button v-if="isModal" @click="closeModal" variant="secondary" class="ml-2 px-4">Close</b-button>
                <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
            </div>
        </b-form>
    </div>
</template>

<script>
import { required, numeric, maxLength, ipAddress } from 'vuelidate/lib/validators';
import DatePicker from 'vue2-datepicker';
import moment from 'moment';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
import ErrorHandlerMixins from '../../mixins/errorHandlerMixins';
const backend = new Backend();

export default {
    name: 'DevicesAddEditPage',
    mixins: [ ErrorHandlerMixins ],
    components: {
        Preloader,
        DatePicker
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            locations: [],
            supportContracts: [],
            deviceModels: [],
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id',
        'subId',
        'isModal'
    ],
    validations: {
        form: {
            number: {
                numeric,
                maxLength: maxLength(6)
            },
            location: {
                required
            },
            support_contract: { },
            device_model: {
                required
            },
            serial_number: {
                required
            },
            hostname: { },
            management_ip: {
                ipAddress
            },
            contract_start_date: { },
            contract_end_date: { }
        }
    },
    methods: {
        closeModal() {
            this.clearForm();
            this.$emit('close-modal');
        },
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Devices' : 'Add Devices';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let subId = this.$route.params.subId;
                backend.getRequest('services/devices/' + subId)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
            backend.getRequest('services/locations')
                .then((response) => {
                    this.locations = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);

            backend.getRequest('services/support_contracts')
                .then((response) => {
                    this.supportContracts = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.number
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);

            backend.getRequest('services/device_models/')
                .then((response) => {
                    this.deviceModels = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.device_manufacturer__str.concat(' ', item.name)
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = Object.assign({}, this.form);
            delete data.id;
            delete data.device_manufacturer;
            if (!data.number) {
                delete data.number;
            }
            if (!data.management_ip) {
                delete data.management_ip;
            }
            if (data.contract_start_date) {
                data.contract_start_date = moment(data.contract_start_date).format('YYYY-MM-DD');
            }
            if (data.contract_end_date) {
                data.contract_end_date = moment(data.contract_end_date).format('YYYY-MM-DD');
            }
            this.isSaving = true;
            let subId = this.$route.params.subId;
            if (this._props.pageType === 'add') {
                backend.postRequest('services/devices/', data)
                    .then((response) => {
                        let name = response.data.number;
                        this.errorMessage = null;
                        this.successMessage = `${name} was added successfully`;
                        this.isSaving = false;
                        this.clearForm();
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/devices/' + subId + '/', data)
                    .then((response) => {
                        let name = response.data.number;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `${name} was changed successfully`;
                        this.form.number = response.data.number;
                    })
                    .catch(this.onSaveError);
            }
        },
        clearForm() {
            this.form = {
                number: null,
                location: this.$route.params.id,
                support_contract: null,
                device_manufacturer: null,
                device_model: null,
                serial_number: null,
                hostname: '',
                management_ip: null,
                contract_start_date: null,
                contract_end_date: null
            };
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
