<template>
    <div>
        <b-navbar toggleable="md" variant="primary" type="dark" fixed="top">
            <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>
            
            <b-navbar-brand class="d-none d-sm-inline-block">Netportal</b-navbar-brand>

            <b-navbar-nav class="d-none d-md-inline-block">
                <b-button
                    v-bind:class="{ 'is-active': subHeaderContent==='services' }"
                    variant="primary"
                    @click.stop="setSubHeaderContent('services')"
                >ServiceDB</b-button>
                <b-button
                    class="ml-1"
                    v-bind:class="{ 'is-active': subHeaderContent==='vBill' }"
                    variant="primary"
                    @click.stop="setSubHeaderContent('vBill')"
                >vBill</b-button>
                <b-button
                    class="ml-1"
                    v-bind:class="{ 'is-active': subHeaderContent==='admin' }"
                    variant="primary"
                    @click.stop="setSubHeaderContent('admin')"
                >Admin</b-button>
            </b-navbar-nav>

            <b-collapse is-nav id="nav_collapse" class="text-left">
                <div class="nav-collapse">
                    <b-navbar-nav class="d-inline-block d-md-none nav-1">
                        <b-nav-item-dropdown text="ServiceDB" class="nav-item-dropdown">
                            <b-dropdown-item  v-b-toggle.nav_collapse class="link" :to="{ name: 'circuits' }">
                                <span>Circuits</span>
                            </b-dropdown-item>
                            <b-dropdown-item  v-b-toggle.nav_collapse class="link" :to="{ name: 'devices' }">
                                <span>Devices</span>
                            </b-dropdown-item>
                            <b-dropdown-item  v-b-toggle.nav_collapse class="link" :to="{ name: 'locations' }">
                                <span>Locations</span>
                            </b-dropdown-item>
                        </b-nav-item-dropdown>

                        <b-nav-item-dropdown text="vBill" class="nav-item-dropdown">
                            <b-dropdown-item  v-b-toggle.nav_collapse class="link">
                                <span>Client Reports</span>
                            </b-dropdown-item>
                            <b-dropdown-item  v-b-toggle.nav_collapse class="link">
                                <span>Consolidated Reports</span>
                            </b-dropdown-item>
                        </b-nav-item-dropdown>

                        <b-nav-item-dropdown text="Admin" class="nav-item-dropdown">
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'carriers' }">
                                <span>Carriers</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'circuitSpeeds' }">
                                <span>Circuit Speeds</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'circuitTypes' }">
                                <span>Circuit Types</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'deviceManufacturers' }">
                                <span>Device Manufacturers</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'deviceModels' }">
                                <span>Device Models</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'supportContracts' }">
                                <span>Support Contracts</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'supportOrganizations' }">
                                <span>Support Organizations</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'clients' }">
                                <span>Clients</span>
                            </b-dropdown-item>
                            <b-dropdown-item v-b-toggle.nav_collapse class="link" :to="{ name: 'partners' }">
                                <span>Partners</span>
                            </b-dropdown-item>
                        </b-nav-item-dropdown>
                    </b-navbar-nav>

                    <b-navbar-nav class="nav-2">
                        <b-form class="mr-3">
                            <b-form-input
                                v-model="globalSearch"
                                class="global-search"
                                placeholder="Global Search"
                            ></b-form-input>
                            <b-button
                                :disabled="!globalSearch.length"
                                type="submit"
                                @click="onGlobalSearch"
                                variant="secondary"
                                class="global-search-button px-3"
                            >
                                <i class="fa fa-search"></i>
                            </b-button>
                        </b-form>
                    </b-navbar-nav>

                    <b-navbar-nav class="ml-md-auto nav-3">
                        <b-nav-item-dropdown :text="'Profile'" right  class="nav-item-dropdown">
                            <b-dropdown-item v-b-toggle.nav_collapse class="d-md-none justify-content-md-end pr-md-2" :to="{ name: 'changePassword' }">
                                <span class="ml-3 ml-md-0">
                                    Change Password
                                </span>
                            </b-dropdown-item>
                            <b-dropdown-item class="d-none d-md-flex justify-content-md-end pr-md-2" :to="{ name: 'changePassword' }">
                                <span class="ml-3 ml-md-0">
                                    Change Password
                                </span>
                            </b-dropdown-item>
                            <b-dropdown-item @click="logout" class="d-md-flex justify-content-md-end pr-2">
                                <span class="ml-3 ml-md-0">
                                    Log Out
                                    <span slot="button-content" class="fa fa-lg fa-sign-out"></span>
                                </span>
                            </b-dropdown-item>
                        </b-nav-item-dropdown>
                    </b-navbar-nav>
                </div>
            </b-collapse>

        </b-navbar>

        <admin-sub-header :content="subHeaderContent"></admin-sub-header>
    </div>
</template>

<script>
import AdminSubHeader from './AdminSubHeader';

export default {
    name: 'AdminHeader',
    components: {
        AdminSubHeader
    },
    data() {
        return {
            globalSearch: '',
            subHeaderContent: 'services'
        };
    },
    mounted() {
        this.$nextTick(() => {
            window.addEventListener('resize', this.checkSubHeaderHeight);
            this.checkSubHeaderHeight();
        });
    },
    watch: {
        '$route': 'onRouteChange'
    },
    methods: {
        logout() {
            localStorage.removeItem('token');
            this.$router.push({ name: 'login' });
        },
        onGlobalSearch(e) {
            e.preventDefault();
            this.$router.push({ name: 'globalSearch', query: { query: this.globalSearch } });
        },
        onRouteChange() {
            if (this.$route.name !== 'globalSearch') {
                this.globalSearch = '';
            }
        },
        setSubHeaderContent(value) {
            this.subHeaderContent = value;
            setTimeout(this.checkSubHeaderHeight, 100);
        },
        checkSubHeaderHeight() {
            if (document.documentElement.clientWidth < 768) {
                document.querySelector('.main-content').style.marginTop = '45px';
                return;
            }
            let subHeaderHeight = document.querySelector('.subheader-nav').offsetHeight;
            if (subHeaderHeight < 60) {
                document.querySelector('.main-content').style.marginTop = '35px';
            } else {
                document.querySelector('.main-content').style.marginTop = '75px';
            }
        }
    }
};
</script>

<style scoped>
    .navbar {
        z-index: 1050;
    }
    .navbar-nav {
        text-align: left;
    }
    .global-search {
        display: inline-block;
        width: 200px;
        border-radius: 0.25rem 0 0 0.25rem;
    }
    .global-search-button {
        position: relative;
        top: -1px;
        z-index: 1;
        border-radius: 0 0.25rem 0.25rem 0;
    }
    .dropdown .dropdown-menu .dropdown-item {
        display: flex;
    }

    .navbar-nav .dropdown-menu {
        position: absolute ;
        /* display: flex; */
    }

    .dropdown-item {
        position: relative;
        height: 40px;
        padding: 0;
        min-width: 260px;
        line-height: 40px
    }

    .dropdown-item:focus {
        outline: none;
    }

    .navbar-toggler:focus {
        outline: none;
    }

    .link {
        width: 100%;
        position: relative;
        padding-left: 15px;
        padding-right: 15px;
    }

    .router-link span {
        width: 100%;
    }

    .is-active {
        background-color: #0069d9;
    }
    .nav-collapse {
        display: flex;
    }

    #nav_collapse {
        padding-left: 15px;
        align-items: flex-end;
        flex-direction: column
    }

    @media screen and (max-width: 767px) {
        .navbar {
            box-shadow: 0 1px 5px rgba(66, 66, 66 ,0.35);
        }
        .nav-collapse {
            display: flex;
            flex-direction: column;
            align-items: normal;
            padding-top: 10px;
        }
        .nav-1 {
            order: 2;
        }
        .nav-2 {
            order: 1;
        }
        .nav-3 {
            order: 3;
        }
    }

</style>
<style>
    @media screen and (max-width: 767px) {
        .nav-item-dropdown > div {
            background: inherit;
        }
        .nav-item-dropdown > div > a,
        .nav-item-dropdown > div > a:focus {
            background: inherit;
            color: #ffffff;
        }
        .nav-item-dropdown > div > a:hover {
            color: #ffffff;
            background: inherit;
            opacity: 0.8;
        }
        .nav-item-dropdown > div > a.active {
            font-weight: 700;
            background-color: rgba(255, 255, 255, 0.2);
        }
    }
</style>
