<template>
    <div>
        <template-for-get-pages-with-add-info
            title="Circuits"
            link="services/circuits/"
            :linkParams="{search: query}"
            :keys="circuitsKeys"
            :moreInfoLinks="['services/circuit_links?circuit=']"
            :moreInfoColumnsTitle="['Links']"
            :moreInfoLinkAbsolute="true"
            :formatFields="{ number: 'C000000' }"
            :rights="{ subTable: { editing: false }}"
            :moreInfoKeys="circuitsMoreInfoKeys"
        ></template-for-get-pages-with-add-info>
        <template-for-get-pages
            title="Locations"
            link="services/locations/"
            :linkParams="{search: query}"
            :formatFields="{ number: '$L000000' }"
            :keys="locationsKeys"
        ></template-for-get-pages>
        <template-for-get-pages
            title="Devices"
            link="services/devices/"
            :linkParams="{search: query}"
            :formatFields="{ number: '$D000000' }"
            :keys="devicesKeys"
        ></template-for-get-pages>
    </div>
</template>
<script>
import Preloader from '../Preloader';
import TemplateForGetPages from './TemplateForGetPages';
import TemplateForGetPagesWithAddInfo from './TemplateForGetPagesWithAddInfo';
import { circuitsKeys, circuitsMoreInfoKeys, devicesKeys, locationsKeys } from '../../services/tableKeys';

export default {
    name: 'GlobalSearchPage',
    data() {
        return {
            query: this.$route.query.query,
            circuitsKeys: this.makeNonSortable(circuitsKeys),
            circuitsMoreInfoKeys: circuitsMoreInfoKeys,
            devicesKeys: this.makeNonSortable(devicesKeys),
            locationsKeys: this.makeNonSortable(locationsKeys)
        };
    },
    watch: {
        '$route.query': 'changeQuery'
    },
    components: {
        Preloader,
        TemplateForGetPages,
        TemplateForGetPagesWithAddInfo
    },
    props: [
        'title',
        'link',
        'formatFields',
        'keys'
    ],
    methods: {
        changeQuery() {
            this.query = this.$route.query.query;
        },
        makeNonSortable(keys) {
            return keys.map((item) => {
                item.sortable = false;
                return item;
            });
        }
    }
};
</script>

