<template>
    <div :class="!isModal ? 'white-card px-4 py4 add-edit-page' : 'px-2' ">
        <div v-if="!isModal">
            <div class="page-breadcrumb">
                <router-link
                    class=""
                    :to="{ name: 'locations' }"
                >
                    Locations /
                </router-link>
            </div>
            <h3 class="page-title">{{ title }}</h3>
        </div>
        <b-form @submit="onSubmit" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Number" label-for="inputNumber">
                    <b-form-input
                        id="inputNumber"
                        v-model.trim="$v.form.number.$model"
                        type="number"
                        :class="{ 'is-invalid': $v.form.number.$error }"
                    >
                    </b-form-input>
                    <b-form-invalid-feedback>
                        This field must contain maximum 6 digits
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Name" label-for="inputName" class="required">
                    <b-form-input
                        id="inputName"
                        v-model.trim="$v.form.name.$model"
                        :class="{ 'is-invalid': $v.form.name.$error }"
                    >
                    </b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
         
                <b-form-group label="Address" label-for="inputAddress">
                    <div class="mb-3">
                        <b-form-input
                            id="inputAddress"
                            v-model="$v.form.address.location.$model"
                            ref="inputAddress"
                            placeholder="Enter a location"
                            :class="{ 'is-invalid': $v.form.address.location.$error }"
                        ></b-form-input>
                        <b-form-invalid-feedback>
                            This field is required
                        </b-form-invalid-feedback>
                    </div>
                    <b-row>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.address_information.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Address Information"
                            ></b-form-input>
                        </b-col>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.level.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Level"
                            ></b-form-input>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.sub_address_type.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Sub Address Type"
                            ></b-form-input>
                        </b-col>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.number.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Number"
                            ></b-form-input>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.street_number.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Street Number"
                            ></b-form-input>
                        </b-col>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.suffix.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Suffix"
                            ></b-form-input>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.street_name.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Street Name"
                            ></b-form-input>
                        </b-col>
                        <b-col md="4">
                            <b-select
                                v-model="$v.form.address.type.$model"
                                class="address-info"
                                size="sm"
                                :options="types"
                                placeholder="Type"
                            ></b-select>
                        </b-col>
                        <b-col md="4">
                            <b-select
                                v-model="$v.form.address.streetSuffix.$model"
                                class="address-info"
                                size="sm"
                                :options="streetSuffixes"
                                placeholder="Street suffix"
                            ></b-select>
                        </b-col>
                    </b-row>
                    <b-row>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.suburb.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Suburb"
                            ></b-form-input>
                        </b-col>
                        <b-col md="4">
                            <b-select
                                v-model="$v.form.address.state.$model"
                                class="address-info"
                                size="sm"
                                :options="states"
                                placeholder="State"
                            ></b-select>
                        </b-col>
                        <b-col md="4">
                            <b-form-input
                                v-model="$v.form.address.postcode.$model"
                                class="address-info"
                                size="sm"
                                type="text"
                                placeholder="Postcode"
                            ></b-form-input>
                        </b-col>
                    </b-row>
                </b-form-group>
                <b-form-group label="Client" label-for="inputClient">
                    <b-form-select
                        id="inputClient"
                        :options="clients"
                        v-model="$v.form.client.$model"
                        :class="{ 'is-invalid': $v.form.client.$error }"
                    ></b-form-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-button type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
                <b-button v-if="isModal" @click="closeModal" variant="secondary" class="ml-2 px-4">Close</b-button>
            </div>
        </b-form>
    </div>
</template>
<script>
import { required, numeric, maxLength } from 'vuelidate/lib/validators';
import GoogleMapsLoader from 'google-maps';
import pickBy from 'lodash/pickBy';
import { types, states, streetSuffixes } from '../../services/addressesForm';
import ErrorHandlerMixins from '../../mixins/errorHandlerMixins';
import Backend from '../../services/backend';

const backend = new Backend();

export default {
    name: 'LocationsAddEditPage',
    mixins: [ ErrorHandlerMixins ],
    data() {
        return {
            clients: [],
            addressId: null,
            form: {
                address: {}
            },
            types: types,
            states: states,
            streetSuffixes: streetSuffixes,
            successMessage: '',
            errorMessage: '',
            errorFetching: false
        };
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    validations: {
        form: {
            number: {
                numeric,
                maxLength: maxLength(6)
            },
            name: {
                required
            },
            client: {

            },
            address: {
                location: { },
                address_information: { },
                sub_address_type: { },
                street_number: { },
                street_name: { },
                suburb: { },
                level: { },
                number: { },
                suffix: { },
                type: { },
                state: { },
                streetSuffix: { },
                postcode: { }
            }
        }
    },
    created() {
        this.handlerForRouteChange();
    },
    mounted() {
        this.$nextTick(this.activatePlacesSearch);
    },
    props: [
        'pageType',
        'id',
        'isModal'
    ],
    methods: {
        closeModal() {
            this.clearForm();
            this.$emit('close-modal');
        },
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Locations' : 'Add Locations';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/locations/' + id)
                    .then((response) => {
                        this.form = response.data;
                       
                        try {
                            this.form.address = JSON.parse(response.data.address);
                        } catch (e) {
                            this.form.address = {};
                        }
                        console.log(this.form);
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
 
            backend.getRequest('vbill/clients')
                .then((response) => {
                    this.clients = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = pickBy(this.form, (item) => item);
            data.address = JSON.stringify(pickBy(this.form.address, (item) => item));
            if (this._props.pageType === 'add') {
                backend.postRequest('services/locations/', data)
                    .then((response) => {
                        let addressName = response.data.name;
                        this.errorMessage = null;
                        this.successMessage = `${addressName} was added successfully`;
                        this.clearForm();
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/locations/' + this.$route.params.id + '/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `${name} was changed successfully`;
                        this.form.number = response.data.number;
                    })
                    .catch(this.onSaveError);
            }
        },
        activatePlacesSearch() {
            const gmapFilledInputs = {
                street_number: ['short_name', 'street_number'],
                route: ['long_name', 'street_name'],
                locality: ['long_name', 'suburb'],
                administrative_area_level_1: ['short_name', 'state'],
                postal_code: ['short_name', 'postcode']
            };

            GoogleMapsLoader.LIBRARIES = ['places'];
            GoogleMapsLoader.KEY = 'AIzaSyBFQ8tzIYRVM1ZVVeMwx8iy12igK2PZZ6g';
            //GoogleMapsLoader.KEY = 'AIzaSyDHF3s9hZ2GSTStkuxLrhcLKKf3YHJTsZ0';
            GoogleMapsLoader.VERSION = 'quarterly';
            GoogleMapsLoader.LANGUAGE = 'en';
            GoogleMapsLoader.load(google => {
                let input = this.$refs.inputAddress.$el;
                if (!input) {
                    return;
                }
                let options = {
                    types: ['address']
                };

                let autocomplete = new google.maps.places.Autocomplete(input, options);
                autocomplete.addListener('place_changed', () => {
                    let place = autocomplete.getPlace();
                    for (let key in gmapFilledInputs) {
                        let inputKey = gmapFilledInputs[key][1];
                        this.form.address[inputKey] = null;
                    }
                    for (let i = 0; i < place.address_components.length; i++) {
                        let addressType = place.address_components[i].types[0];
                        if (gmapFilledInputs[addressType]) {
                            let value = place.address_components[i][gmapFilledInputs[addressType][0]];
                            let inputKey = gmapFilledInputs[addressType][1];
                            this.form.address[inputKey] = value;
                        }
                    }
                    this.form.address.location = place.formatted_address;
                });

                // street_number, postal_code
            });
        },
        clearForm() {
            this.form = {
                number: '',
                name: '',
                client: '',
                address: {
                    location: null,
                    address_information: null,
                    sub_address_type: null,
                    street_number: null,
                    street_name: null,
                    suburb: null,
                    level: null,
                    number: null,
                    suffix: null,
                    type: null,
                    state: null,
                    streetSuffix: null,
                    postcode: null
                }
            };
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
    .address-info {
        margin-bottom: 10px;
    }
</style>
