<template>
    <div>
        <b-row v-if="!this.linkParams">
            <b-col
                md="12"
                class="text-align-left"
            >
                <router-link
                    class="add-button mr-4"
                    :to="{ path: $router.currentRoute.path + '/add' }"
                >
                    <b-button
                        variant="primary"
                        class=""
                    >Create new</b-button>
                </router-link>
                <b-form-input
                    class="search-input"
                    id="searchInput"
                    placeholder="Type to search"
                    @input="onSearch"
                    v-model="search"
                ></b-form-input>
            </b-col>
        </b-row>
        <b-row class="white-card mt-3">
            <transition name="fade">
                <b-col cols="12">
                    <h3 class="page-title">{{ title }}</h3>
                    <div v-if="isFetching">
                        <h6>Loading</h6> <preloader size="40px"></preloader>
                    </div>
                     <b-card
                        v-if="errorFetching"
                        border-variant="danger"
                        text-variant="danger"
                        class="mb-3"
                    >
                        {{ errorMessage }}
                    </b-card>
                    <b-table
                        v-if="!isFetching && !errorFetching"
                        :show-empty="true"
                        :items="items"
                        :fields="fields"
                        responsive
                        :no-local-sorting="true"
                        @sort-changed="onSortChange"
                        :sort-by="sortBy"
                        :sort-desc="sortDesc"
                        striped
                        class="table-for-get-pages"
                    >
                        <template slot="_delete" slot-scope="data">
                            <b-button
                                v-if="!isDeletingItems[data.item.id]"
                                variant="link"
                                class="py-0 delete-button"
                                @click="onDelete(data.item.id, data.item)"
                            >
                                <i class="fa fa-trash"></i>
                            </b-button>
                            <preloader v-if="isDeletingItems[data.item.id]" size="20px"></preloader>
                        </template>
                        <template slot="_edit" slot-scope="data">
                            <router-link
                                class="add-button"
                                :to="{ path: relativePath + data.item.id + '/edit' }"
                            >
                                <b-button
                                    variant="link"
                                    class="py-0 edit-button fa fa-edit"
                                ></b-button>
                            </router-link>
                        </template>
                        <template
                            v-for="(link, linkIndex) in moreInfoLinks"
                            :slot="moreInfoColumnsTitle && moreInfoColumnsTitle[linkIndex] || '_'.concat(link)"
                            slot-scope="row">
                            <b-button
                                :key="link"
                                variant="link"
                                class="py-0 delete-button"
                                @click.stop="onMoreInfo(row, link, linkIndex)"
                            >
                                <i class="fa fa-info"></i>
                            </b-button>
                        </template>

                        <template slot="row-details" slot-scope="row">
                            <!--Create new subtable button-->
                            <div class="text-align-left">
                                <router-link
                                    class="link"
                                    :to="{ path: relativePath + row.item.id + currentMoreInfoLink + 'add' }"
                                >
                                    <b-button variant="primary" class="mb-2 px-4" v-if="!rights || rights.subTable.editing">
                                        Create new {{currentAdditionalInfoLabel}}<i class="fa fa-plus ml-2"></i>
                                    </b-button>
                                </router-link>
                            </div>
                            <!--Subtable-->
                            <div v-if="additionalInfoIsFetching" class="text-align-left">
                                Loading <preloader size="20px" class="ml-2"></preloader>
                            </div>
                            <transition-group name="fade">
                                <b-card
                                    v-if="errorAdditionalInfoFetching"
                                    border-variant="danger"
                                    text-variant="danger"
                                    class="mb-3"
                                    key="1"
                                >
                                    {{ errorMessage }}
                                </b-card>
                                <b-table
                                    :show-empty="true"
                                    v-if="!additionalInfoIsFetching"
                                    :items="row.item.additionalInfo"
                                    :fields="additionalInfoFields"
                                    :class="{ 'table-auto-width': rights && !rights.subTable.editing }"
                                    responsive
                                    key="2"
                                >
                                    <template slot="_edit" slot-scope="data" >
                                        <router-link
                                            v-if="currentRow"
                                            class="add-button"
                                            :to="{ path: relativePath + currentRow.item.id + currentMoreInfoLink + data.item.id + '/edit'}"
                                        >
                                            <b-button
                                                variant="link"
                                                class="py-0 edit-button fa fa-edit"
                                            ></b-button>
                                        </router-link>
                                    </template>
                                    <template slot="_delete" slot-scope="data" class="delete">
                                        <b-button
                                            v-if="!isDeletingAdditionalItems[data.item.id]"
                                            variant="link"
                                            class="py-0 delete-button"
                                            @click="onDeleteAdditionalInfo(data.item.id, row, data.item)"
                                        >
                                            <i class="fa fa-trash"></i>
                                        </b-button>
                                        <preloader v-if="isDeletingAdditionalItems[data.item.id]" size="20px"></preloader>
                                    </template>
                                </b-table>
                            </transition-group>

                            <b-button size="sm" @click="row.toggleDetails">Hide Details</b-button>
                        </template>
                    </b-table>
                    <router-view></router-view>
                </b-col>
            </transition>
        </b-row>
        <b-pagination-nav
            v-if="!isFetching && numberOfPages > 1"
            :link-gen="paginationLinkGen"
            :number-of-pages="numberOfPages"
            v-model="currentPage"
            class="pagination"
        />
    </div>
</template>
<script>
import Backend from '../../services/backend';
import Preloader from '../Preloader';
import StringMask from 'string-mask';
import GetPagesMixins from '../../mixins/getPagesMixins';

const backend = new Backend();

export default {
    name: 'TemplateForGetPagesWithAddInfo',
    mixins: [ GetPagesMixins ],
    data() {
        return {
            items: [],
            isDeletingItems: {},
            isDeletingAdditionalItems: {},
            fields: [],
            additionalInfoFields: [],
            lastShowedKey: '',
            currentRow: null,
            currentMoreInfoLink: '',
            currentAdditionalInfoLabel: '',
            isFetching: false,
            additionalInfoIsFetching: false,
            errorFetching: false,
            errorAdditionalInfoFetching: false,
            errorMessage: '',
            numberOfPages: null,
            currentPage: null,
            sortBy: null,
            sortDesc: null,
            search: this.$route.query.search
        };
    },
    components: {
        Preloader
    },
    props: [
        'title',
        'link',
        'linkParams',
        'moreInfoLinks',
        'moreInfoLinkAbsolute',
        'moreInfoColumnsTitle',
        'moreInfoKeys',
        'formatFields',
        'formatLoadMoreFields',
        'rights',
        'keys'
    ],
    watch: {
        '$route.query': 'loadValues'
    },
    mounted() {
        this.loadValues();
    },
    computed: {
        relativePath() {
            return this._props.link.replace(/[^/]*\//, '');
        }
    },
    methods: {
        onDeleteAdditionalInfo(id, row, item) {
            let rowId = row.item.id;
            let name = item.name || item.value || item.number || 'it';
            if (!window.confirm('Do you really want to delete ' + name + '?')) {
                return;
            }
            this.isDeletingAdditionalItems = Object.assign({}, this.isDeletingAdditionalItems, { [id]: true });
            backend.deleteRequest(this._props.link + rowId + '/' + this.currentMoreInfoLink + '/' + id)
                .then(() => {
                    this.isDeletingAdditionalItems = Object.assign({}, this.isDeletingAdditionalItems, { [id]: null });
                    this.loadMoreInfoValues(row);
                })
                .catch((error) => {
                    let errorText = error.response.data.error || 'Something went wrong. Please try again later';
                    this.$store.dispatch('addNotification', { text: errorText, variant: 'danger' });
                    this.isDeletingAdditionalItems = Object.assign({}, this.isDeletingAdditionalItems, { [id]: null });
                });
        },
        onMoreInfo(row, moreInfoLink, linkIndex) {
            if (this.currentRow && this.currentRow.index !== row.index) {
                if (this.currentRow.item._showDetails) {
                    this.currentRow.toggleDetails();
                }
            } else {
                if (row.item._showDetails) {
                    row.toggleDetails();
                    if (this.lastShowedKey === row.field.key) {
                        this.lastShowedKey = '';
                        this.currentRow = null;
                        return;
                    }
                }
            }
            this.currentRow = row;
            this.additionalInfoIsFetching = true;
            this.currentAdditionalInfoLabel = (row.field.label || '').toLowerCase();
            this.currentMoreInfoLink = moreInfoLink;
            row.toggleDetails();
            this.lastShowedKey = row.field.key;
            this.loadMoreInfoValues(row, linkIndex);
        },
        loadMoreInfoValues(row, linkIndex) {
            let moreInfoLink = this.link + row.item.id + this.currentMoreInfoLink;
            if (this._props.moreInfoLinkAbsolute) {
                moreInfoLink = this.currentMoreInfoLink + row.item.id;
            }
            this.additionalInfoIsFetching = true;
            this.errorAdditionalInfoFetching = false;

            backend.getRequest(moreInfoLink)
                .then(response => {
                    let results = response.data.results;
                    if (results.length === 0) {
                        this.additionalInfoIsFetching = true;
                        row.item.additionalInfo = response.data.results;
                        this.additionalInfoIsFetching = false;
                        return;
                    }
                    this.additionalInfoIsFetching = false;
                    let keys = this._props.moreInfoKeys && this._props.moreInfoKeys[linkIndex] ||
                        Object.keys(response.data.results[0]);
                    if (!this.rights || this.rights.subTable.editing) {
                        this.additionalInfoFields = keys.concat('_edit', '_delete');
                    } else {
                        this.additionalInfoFields = keys;
                    }

                    if (this.formatLoadMoreFields) {
                        let formatter = new StringMask(this.formatLoadMoreFields.number, { reverse: true });
                        row.item.additionalInfo = results.map(item => {
                            if (item.number) {
                                item.number = formatter.apply(item.number);
                            }
                            return item;
                        });
                    } else {
                        row.item.additionalInfo = results;
                    }
                    this.errorAdditionalInfoFetching = false;
                    this.additionalInfoIsFetching = false;
                })
                .catch(error => {
                    this.errorMessage = error.response && error.response.data &&
                        JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                        'Something went wrong. Please try again later';
                    this.errorAdditionalInfoFetching = true;
                    this.additionalInfoIsFetching = false;
                });
        },
        loadValues() {
            this.isFetching = true;
            let sortDirection = !this.$router.currentRoute.query.sortDesc ? '' : '-';
            let sortBy = this.$router.currentRoute.query.sortBy;
            sortBy = sortBy && sortBy.replace(/__str$/, '');
            let search = this.$router.currentRoute.query.search;
            this.currentPage = this.$route.query.page ? parseInt(this.$route.query.page) : 1;
            let params = {
                page: this.currentPage,
                ordering: sortDirection + sortBy,
                search: search
            };
            if (this.linkParams) {
                params = Object.assign(params, this.linkParams);
            }
            backend.getRequest(this._props.link, params)
                .then(response => {
                    let keys = this._props.keys || Object.keys(response.data.results[0]);
                    let links = this._props.moreInfoColumnsTitle || this.moreInfoLinks.map(value => '_'.concat(value));
                    this.fields = keys.concat(links, '_edit', '_delete');
                    if (this.formatFields) {
                        let formatter = new StringMask(this.formatFields.number, { reverse: true });
                        this.items = response.data.results.map(item => {
                            item.number = formatter.apply(item.number).toUpperCase();
                            return item;
                        });
                    } else {
                        this.items = response.data.results;
                    }
                    this.numberOfPages = response.data.num_pages;
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
        }
    }
};
</script>

<style scoped>
    .delete-button {
        color: #dc3545;
        line-height: 0;
    }
    .edit-button {
        color: #6c757d;
        line-height: 0;
    }
    .text-align-left {
        text-align: left;
    }
</style>
<style>
    .fade-enter-active, .fade-leave-active {
        transition: opacity ease-in-out 0.3s;
    }
    .fade-enter, .fade-leave-to {
        opacity: 0;
    }
    .table-for-get-pages td {
        white-space: nowrap;
    }
    .table-for-get-pages .table-responsive:not(.table-auto-width) th:nth-last-child(-n+2),
    .table-for-get-pages .table-responsive:not(.table-auto-width) td:nth-last-child(-n+2) {
        padding-left: 15px;
        padding-right: 15px;
        width: 20px;
    }
    .search-input {
        display: inline-block;
        width: 250px;
        position: relative;
        top: 1px;
    }
    @media screen and (max-width: 438px) {
        .add-button button {
            margin-bottom: 10px;
            width: 100%;
        }
        .search-input {
            width: 100% !important;
            text-align: center;
        }
    }
</style>
