<template>
    <div class="notifications-container">
        <transition-group name="fade">
            <div v-for="(notification, index) in notifications" :key="index">
                <b-alert
                    :variant="notification.variant"
                    class="notification-instance mb-2"
                    show
                >
                    {{ notification.text }}
                </b-alert>
                <br>
            </div>
        </transition-group>
    </div>
</template>

<script>

export default {
    name: 'Notifications',
    computed: {
        notifications() {
            return this.$store.state.notifications;
        }
    }
};
</script>

<style scoped>
    .notifications-container {
        position: fixed;
        top: 10px;
        z-index: 100000;
        text-align: center;
        padding: 0 5px;
        width: 100%;
    }
    .notification-instance {
        display: inline-block;
        padding-left: 70px;
        padding-right: 70px;
    }
</style>
