<template>
 <div class="white-card px-4 py-4">
        <h3 class="page-title">Change Password</h3>
        <b-form class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div>
                <b-form-group label="Old password" label-for="inputOldPassword">
                    <b-form-input
                        id="inputOldPassword"
                        type="password"
                        v-model="$v.form.old_password.$model"
                        v-bind:class="{ 'is-invalid': $v.form.old_password.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="New password" label-for="inputNewPassword">
                    <b-form-input 
                        id="inputNewPassword"
                        type="password"
                        v-model="$v.form.new_password.$model"
                        :class="{ 'is-invalid': $v.form.new_password.$error }"
                    ></b-form-input>
                    <b-form-invalid-feedback >
                        Password must have at least 8 letters
                    </b-form-invalid-feedback>
                </b-form-group>
               
                <b-form-group label="Repeat new password" label-for="inputRepeatPassword">
                    <b-form-input 
                        id="inputRepeatPassword"
                        type="password"
                        v-model="$v.form.repeat_password.$model"
                        :class="{ 'is-invalid': $v.form.repeat_password.$error }"
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        Passwords must be identical
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
                <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
            </div>
        </b-form>
    </div>    
</template>
<script>
import { required, minLength, sameAs } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
const backend = new Backend();

export default {
    name: 'changePasswordPage',
    components: {
        Preloader
    },
    validations: {
        form: {
            old_password: {
                required
            },
            new_password: {
                required,
                minLength: minLength(8)
            },
            repeat_password: {
                required,
                sameAsPassword: sameAs(function() { return this.form.new_password; })
            }
        }
    },
    created() {
        this.clearForm();
    },
    data() {
        return {
            form: {},
            isSaving: false,
            successMessage: null,
            errorMessage: null
        };
    },
    methods: {
        onSubmit(e) {
            e.preventDefault();

            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }

            let data = Object.assign({}, this.form);
            delete data.repeat_password;
            this.isSaving = true;
            backend.postRequest('auth/password/change/', data)
                .then(response => {
                    this.successMessage = response.data.status;
                    this.clearForm();
                })
                .catch(error => {
                    let resData = error.response.data;
                    this.errorMessage = '';
                    for (let key in resData) {
                        this.errorMessage = this.errorMessage.concat(key, ' : ', resData[key], '\n');
                    }
                    this.isSaving = false;
                });
        },
        clearForm() {
            this.isSaving = false;
            this.form = {
                old_password: null,
                new_password: null,
                repeat_password: null
            };
            this.errorMessage = '';
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>
<style scoped>
    .main-container {
        text-align: left;
    }
</style>
