<template>
    <div class="white-card px-4 py-4 add-edit-page">
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'circuits' }"
            >
                Circuits /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
          <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <!--Circuits form-->
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Number" label-for="inputNumber">
                    <b-form-input
                        v-model="$v.form.number.$model"
                        id="inputNumber"
                        label="Number"
                        type="number"
                        v-bind:class="{ 'is-invalid': $v.form.number.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        This field must contain maximum 6 digits
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Circuit type" label-for="inputCircuitType" class="mb-3 required">
                    <b-form-select
                        v-model="$v.form.circuit_type.$model"
                        id="inputCircuitType"
                        :options="circuitTypes"
                        @change="onCircuitTypeChange"
                        v-bind:class="{ 'is-invalid': $v.form.circuit_type.$error }"/>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <transition name="fade">
                    <b-form-group
                        v-if="circuitSubTypes && circuitSubTypes.length"
                        label="Circuit subtype"
                        label-for="inputCircuitSubType"
                        class="mb-0"
                    >
                        <b-form-select
                            v-model="$v.form.circuit_subtype.$model"
                            id="inputCircuitSubType"
                            :options="circuitSubTypes"
                            class="mb-3"
                            v-bind:class="{ 'is-invalid': $v.form.circuit_subtype.$error }"/>
                        <b-form-invalid-feedback>
                            This field is required
                        </b-form-invalid-feedback>
                    </b-form-group>
                </transition>
                <b-form-group label="Circuit speed" label-for="inputCircuitSpeed" class="mb-0">
                    <b-form-select
                        v-model="$v.form.circuit_speed.$model"
                        id="inputCircuitSpeed"
                        :options="circuitSpeeds"
                        class="mb-3"
                        v-bind:class="{ 'is-invalid': $v.form.circuit_speed.$error }"/>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Carrier" label-for="inputCarrier" class="mb-0">
                    <b-form-select
                        v-model="$v.form.carrier.$model"
                        id="inputCarrier"
                        :options="carriers"
                        class="mb-3"
                        v-bind:class="{ 'is-invalid': $v.form.carrier.$error }"/>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Carrier Vlan" label-for="inputCarrierVlan">
                    <b-form-input
                        v-model="$v.form.carrier_vlan.$model"
                        id="inputCarrierVlan"
                        v-bind:class="{ 'is-invalid': $v.form.carrier_vlan.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Carrier Service" label-for="inputCarrierService">
                    <b-form-input
                        v-model="$v.form.carrier_service.$model"
                        id="inputCarrierService"
                        v-bind:class="{ 'is-invalid': $v.form.carrier_service.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Contract Start" label-for="inputContractStartDate">
                    <date-picker
                        v-model="$v.form.contract_start_date.$model"
                        lang="en"
                        format="YYYY-MM-DD"
                    ></date-picker>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Contract End" label-for="inputContractEndDate">
                    <date-picker
                        v-model="$v.form.contract_end_date.$model"
                        lang="en"
                        format="YYYY-MM-DD"
                    ></date-picker>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <circuit-links-form :page-type="pageType" :id="id" ref="linksForm"/>
                <b-button
                    @click="addLocationsModalShow = !addLocationsModalShow"
                    variant="secondary"
                    class="button-full-width px-4 mr-3 mb-2">Create New Location
                </b-button>
                <b-button
                    @click="addDevicesModalShow = !addDevicesModalShow"
                    variant="secondary"
                    class="button-full-width px-4 mr-3 mb-2">Create New Device
                </b-button>
                <b-button
                    :disabled="isSaving"
                    type="submit"
                    @click="onSubmit"
                    variant="primary"
                    class="button-full-width px-4 mr-3 mb-2"
                >Submit</b-button>
                <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
            </div>
        </b-form>

        <b-modal v-model="addLocationsModalShow" size="lg">
            <h3 class="page-title" slot="modal-title">Add Locations</h3>
            <locations-add-edit-page
                pageType="add"
                :isModal="true"
                @close-modal="closeLocationsModal()"
            ></locations-add-edit-page>
            <div slot="modal-footer"></div>
        </b-modal>
        <b-modal size="lg" v-model="addDevicesModalShow">
            <h3 class="page-title" slot="modal-title">Add Devices</h3>
            <devices-add-edit-page
                pageType="add"
                :isModal="true"
                @close-modal="closeDevicesModal()"
            >
            </devices-add-edit-page>
            <div slot="modal-footer"></div>
        </b-modal>
    </div>
</template>

<script>
import { required, numeric, maxLength } from 'vuelidate/lib/validators';
import DatePicker from 'vue2-datepicker';
import StringMask from 'string-mask';
import moment from 'moment';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
import CircuitLinksForm from '../CircuitLinksForm';
import DevicesAddEditPage from './DevicesAddEditPage';
import LocationsAddEditPage from './LocationsAddEditPage';
import ErrorHandlerMixins from '../../mixins/errorHandlerMixins';
const backend = new Backend();

export default {
    name: 'CircuitsAddEditPage',
    mixins: [ ErrorHandlerMixins ],
    components: {
        Preloader,
        DatePicker,
        DevicesAddEditPage,
        LocationsAddEditPage,
        CircuitLinksForm
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            id: this.$route.params.id,
            title: '',
            circuitTypes: [],
            circuitSpeeds: [],
            circuitSubTypes: [],
            carriers: [],
            locations: [],
            locationADevices: [],
            locationBDevices: [],
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null,
            addLocationsModalShow: false,
            addDevicesModalShow: false

        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType'
    ],
    validations: {
        form: {
            number: {
                numeric,
                maxLength: maxLength(6)
            },
            circuit_type: {
                required
            },
            circuit_subtype: {},
            circuit_speed: {},
            carrier: {},
            carrier_vlan: {},
            carrier_service: {},
            contract_start_date: {},
            contract_end_date: {}
        }
    },
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Circuits' : 'Add Circuits';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                this.id = this.$route.params.id;
                backend.getRequest('services/circuits/' + this.id)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
            backend.getRequest('services/circuit_types/')
                .then((response) => {
                    this.circuitTypes = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
            backend.getRequest('services/circuit_speeds/')
                .then((response) => {
                    this.circuitSpeeds = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.value
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
            backend.getRequest('services/carriers/')
                .then((response) => {
                    this.carriers = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
            backend.getRequest('services/locations/')
                .then((response) => {
                    let formatter = new StringMask('$L000000', { reverse: true });
                    this.locations = response.data.results.map((item) => {
                        return {
                            value: item.id,
                            text: formatter.apply(item.number).toUpperCase() + ' | ' + item.name
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.form.$invalid) {
                this.$v.form.$touch();
                return;
            }
            let data = Object.assign({}, this.form);
            if (!data.number) {
                delete data.number;
            }
            if (typeof data.contract_start_date === 'object' && data.contract_start_date !== null) {
                data.contract_start_date = moment(data.contract_start_date).format('YYYY-MM-DD');
            }
            if (typeof data.contract_end_date === 'object' && data.contract_end_date !== null) {
                data.contract_end_date = moment(data.contract_end_date).format('YYYY-MM-DD');
            }

            this.isSaving = true;
            if (this._props.pageType === 'add') {
                backend.postRequest('services/circuits/', data)
                    .then((response) => {
                        let name = response.data.number;
                        this.errorMessage = null;
                        this.$store.dispatch('addNotification', {
                            text: `Circuit number ${name} was added successfully`,
                            variant: 'success'
                        });
                        this.isSaving = false;
                        this.clearForm();
                        this.$refs.linksForm.onSubmit(parseInt(response.data.id));
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/circuits/' + this.$route.params.id + '/', data)
                    .then((response) => {
                        this.form.number = response.data.number;
                        let name = response.data.number;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.$store.dispatch('addNotification', {
                            text: `Circuit number ${name} was changed successfully`,
                            variant: 'success'
                        });
                        this.$refs.linksForm.onSubmit(parseInt(this.$route.params.id));
                    })
                    .catch(this.onSaveError);
            }
        },
        onCircuitTypeChange(id) {
            this.form.circuit_subtype = '';
            backend.getRequest('services/circuit_types/' + id + '/circuit_subtypes/')
                .then((response) => {
                    this.circuitSubTypes = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        onSaveError(error) {
            this.isSaving = false;
            this.successMessage = null;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
            this.$store.dispatch('addNotification', {
                text: this.errorMessage,
                variant: 'danger'
            });
        },
        clearForm() {
            this.form = {
                number: '',
                circuit_type: '',
                circuit_subtype: '',
                circuit_speed: '',
                carrier: '',
                carrier_vlan: '',
                carrier_service: '',
                contract_start_date: null,
                contract_end_date: null
            };
            setTimeout(() => this.$v.$reset(), 0);
        },
        closeLocationsModal() {
            this.addLocationsModalShow = false;
            this.$refs.linksForm.loadLocations();
        },
        closeDevicesModal() {
            this.addDevicesModalShow = false;
            this.$refs.linksForm.loadDevices(0);
            this.$refs.linksForm.loadDevices(1);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
    .link-point {
        font-size: 17px;
        font-weight: 700;
        margin-bottom: 10px;
    }
    .link-point span {
        text-transform: uppercase;
    }
    .link-point i {
        font-size: 24px;
        color: #28a745;
    }
</style>
