<template>
    <div class="white-card px-4 py-4 add-edit-page">
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'partners' }"
            >
                Partners /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
        <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Name" label-for="inputName" class="required">
                    <b-form-input
                        v-model="$v.form.name.$model"
                        id="inputName"
                        label="Name"
                        v-bind:class="{ 'is-invalid': $v.form.name.$error }">
                    ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
                <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
            </div>
        </b-form>
    </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
import ErrorHandlerMixins from '../../mixins/errorHandlerMixins';
const backend = new Backend();

export default {
    name: 'PartnersAddEditPage',
    mixins: [ ErrorHandlerMixins ],
    components: {
        Preloader
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id'
    ],
    validations: {
        form: {
            name: {
                required
            }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Partners' : 'Add Partners';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('vbill/partners/' + id)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = this.form;
            this.isSaving = true;
            if (this._props.pageType === 'add') {
                backend.postRequest('vbill/partners/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.errorMessage = null;
                        this.successMessage = `${name} was added successfully`;
                        this.isSaving = false;
                        this.clearForm();
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('vbill/partners/' + this.$route.params.id + '/', data)
                    .then((response) => {
                        let name = response.data.name;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `${name} was changed successfully`;
                    })
                    .catch(this.onSaveError);
            }
        },
        clearForm() {
            this.form = {
                name: ''
            };
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
