<template>
    <div class="white-card px-4 py-4 add-edit-page">
        <div class="page-breadcrumb">
            <router-link
                class=""
                :to="{ name: 'supportContracts' }"
            >
                Support Contracts /
            </router-link>
        </div>
        <h3 class="page-title">{{ title }}</h3>
        <div v-if="isFetching">
            Loading... <preloader size="40px"></preloader>
        </div>
        <b-form v-if="!isFetching" class="main-container">
            <b-card
                v-if="errorMessage"
                border-variant="danger"
                text-variant="danger"
                class="mb-3"
            >
                {{ errorMessage }}
            </b-card>
            <b-card
                v-if="successMessage"
                border-variant="success"
                text-variant="success"
                class="mb-3"
            >
                {{ successMessage }}
            </b-card>
            <div v-show="!errorFetching">
                <b-form-group label="Name" label-for="inputName" class="required">
                    <b-form-input
                        v-model="$v.form.number.$model"
                        id="inputNumber"
                        label="Number"
                        v-bind:class="{ 'is-invalid': $v.form.number.$error }">
                        ></b-form-input>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-form-group label="Support Organization" label-for="inputSupportOrganization">
                    <b-form-select :options="supportOrganizations"
                                   id="inputSupportOrganization"
                                   v-model="$v.form.support_organization.$model"
                                   :class="{ 'is-invalid': $v.form.support_organization.$error }"
                    ></b-form-select>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <b-button :disabled="isSaving" type="submit" @click="onSubmit" variant="primary" class="px-4">Submit</b-button>
                <preloader v-if="isSaving" size="35px" class="ml-3"></preloader>
            </div>
        </b-form>
    </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators';
import Backend from '../../services/backend';
import Preloader from '../Preloader';
import ErrorHandlerMixins from '../../mixins/errorHandlerMixins';
const backend = new Backend();

export default {
    name: 'DeviceModelsAddEditPage',
    mixins: [ ErrorHandlerMixins ],
    components: {
        Preloader
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {
                name: null,
                device_manufacturer: null
            },
            title: '',
            supportOrganizations: [],
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.clearForm();
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id'
    ],
    validations: {
        form: {
            number: {
                required
            },
            support_organization: { }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.title = this._props.pageType === 'edit' ? 'Edit Support Contracts' : 'Add Support Contracts';
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/support_contracts/' + id)
                    .then((response) => {
                        this.form = response.data;
                        this.isFetching = false;
                        this.errorFetching = false;
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
            backend.getRequest('services/support_organizations')
                .then((response) => {
                    this.supportOrganizations = response.data.results.map(item => {
                        return {
                            value: item.id,
                            text: item.name
                        };
                    });
                    this.isFetching = false;
                    this.errorFetching = false;
                })
                .catch(this.onFetchingError);
        },
        onSubmit(e) {
            e.preventDefault();
            if (this.$v.$invalid) {
                this.$v.$touch();
                return;
            }
            let data = this.form;
            this.isSaving = true;
            let id = this.$route.params.id;
            if (this._props.pageType === 'add') {
                backend.postRequest('services/support_contracts/', data)
                    .then((response) => {
                        let number = response.data.number;
                        this.errorMessage = null;
                        this.successMessage = `${number} was added successfully`;
                        this.isSaving = false;
                        this.clearForm();
                    })
                    .catch(this.onSaveError);
            } else if (this._props.pageType === 'edit') {
                backend.putRequest('services/support_contracts/' + id + '/', data)
                    .then((response) => {
                        let number = response.data.number;
                        this.hasBackendError = false;
                        this.errorMessage = null;
                        this.isSaving = false;
                        this.successMessage = `${number} was changed successfully`;
                    })
                    .catch(this.onSaveError);
            }
        },
        clearForm() {
            this.form = Object.assign({}, {
                number: '',
                support_organization: null
            });
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
</style>
