import { debounce } from 'lodash';
import Backend from '../services/backend';
const backend = new Backend();

export default {
    methods: {
        onSortChange(ctx) {
            let currentName = this.$route.name;
            let search = this.$route.query.search;
            this.sortBy = ctx.sortBy;
            this.sortDesc = ctx.sortDesc;
            this.$router.push({ name: currentName, query: { search, sortBy: ctx.sortBy, sortDesc: ctx.sortDesc } });
        },
        onSearch: debounce(function(value) {
            let newRoute = {
                name: this.$route.name,
                query: {}
            };
            if (value) {
                newRoute.query.search = value;
            }
            this.$router.push(newRoute);
        }, 600),
        onDelete(id, item) {
            let name = item.name || item.value || item.number || 'it';
            if (!window.confirm('Do you really want to delete ' + name + '?')) {
                return;
            }
            this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: true });
            backend.deleteRequest(this._props.link + id)
                .then(() => {
                    this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
                    this.loadValues();
                })
                .catch(this.onDeleteError(id));
        },
        onDeleteError(id) {
            return (error) => {
                let errorText = error.response.data.error || 'Something went wrong. Please try again later';
                this.$store.dispatch('addNotification', { text: errorText, variant: 'danger' });
                this.isDeletingItems = Object.assign({}, this.isDeletingItems, { [id]: null });
            };
        },
        onFetchingError(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
            if (error.response && error.response.status === 401) {
                localStorage.removeItem('token');
                this.$router.push({ name: 'login' });
            }
        },
        paginationLinkGen(pageNum) {
            let newRoute = {
                name: this.$route.name,
                query: {
                    page: pageNum
                }
            };
            if (this.$route.query.sortBy) {
                newRoute.query.sortBy = this.$route.query.sortBy;
            }
            if (this.$route.query.sortDesc) {
                newRoute.query.sortDesc = this.$route.query.sortDesc;
            }
            if (this.$route.query.search) {
                newRoute.query.search = this.$route.query.search;
            }
            return newRoute;
        }
    }
};
