import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    strict: true,
    state: {
        address: 'HOME',
        notifications: []
    },

    mutations: {
        pushNotification(state, payload) {
            state.notifications = [].concat(state.notifications, payload);
        },
        removeNotification(state, payload) {
            let notifications = state.notifications.filter((item) => item.text !== payload.text);
            state.notifications = [].concat(notifications);
        }
    },

    actions: {
        addNotification({ commit }, payload) {
            commit('pushNotification', payload);
            let timeToShow = 6000;
            if (payload.variant === 'success') {
                timeToShow = 4000;
            }
            setTimeout(() => {
                commit('removeNotification', payload);
            }, timeToShow);
        }
    }
});
