import Vue from 'vue';
import VueRouter from 'vue-router';
import Login from '../components/Login';
import AdminPage from '../components/AdminPage';
import CarriersPage from '../components/pages/CarriersPage';
import CarriersAddEditPage from '../components/pages/CarriersAddEditPage';
import CircuitSpeedsPage from '../components/pages/CircuitSpeedsPage';
import CircuitSpeedsAddEditPage from '../components/pages/CircuitSpeedsAddEditPage';
import CircuitTypesPage from '../components/pages/CircuitTypesPage';
import CircuitTypesAddEditPage from '../components/pages/CircuitTypesAddEditPage';
import CircuitSubtypesAddEditPage from '../components/pages/CircuitSubtypesAddEditPage';
import CircuitsPage from '../components/pages/CircuitsPage';
import CircuitsAddEditPage from '../components/pages/CircuitsAddEditPage';
import DeviceManufacturersPage from '../components/pages/DeviceManufacturersPage';
import DeviceManufacturersAddEditPage from '../components/pages/DeviceManufacturersAddEditPage';
import DeviceModelsAddEditPage from '../components/pages/DeviceModelsAddEditPage';
import DeviceModelsPage from '../components/pages/DeviceModelsPage';
import LocationsPage from '../components/pages/LocationsPage';
import LocationsAddEditPage from '../components/pages/LocationsAddEditPage';
import DevicesPage from '../components/pages/DevicesPage';
import DevicesAddEditPage from '../components/pages/DevicesAddEditPage';
import SupportContractsPage from '../components/pages/SupportContractsPage';
import SupportContractsAddEditPage from '../components/pages/SupportContractsAddEditPage';
import SupportOrganizationsPage from '../components/pages/SupportOrganizationsPage';
import SupportOrganizationsAddEditPage from '../components/pages/SupportOrganizationsAddEditPage';
import EmptyComponent from '../components/EmptyComponent';
import PartnersPage from '../components/pages/PartnersPage';
import PartnersAddEditPage from '../components/pages/PartnersAddEditPage';
import ClientsPage from '../components/pages/ClientsPage';
import ClientsAddEditPage from '../components/pages/ClientsAddEditPage';
import PhoneRangesAddEditPage from '../components/pages/PhoneRangesAddEditPage';
import RatesAddEditPage from '../components/pages/RatesAddEditPage';
import DiscountsAddEditPage from '../components/pages/DiscountsAddEditPage';
import ChangePasswordPage from '../components/pages/ChangePasswordPage';
import GlobalSearchPage from '../components/pages/GlobalSearchPage';

Vue.use(VueRouter);

const ifNotAuthenticated = (to, from, next) => {
    if (!localStorage.token) {
        next();
        return;
    }
    next({ name: 'adminPage' });
};

const ifAuthenticated = (to, from, next) => {
    if (localStorage.token) {
        next();
        return;
    }
    next({ name: 'login' });
};

export default new VueRouter({
    routes: [
        {
            name: 'adminPage',
            path: '/',
            redirect: {
                name: 'circuits'
            },
            component: AdminPage,
            beforeEnter: ifAuthenticated,
            children: [
                {
                    path: 'services',
                    component: EmptyComponent,
                    children: [
                        {
                            name: 'globalSearch',
                            path: 'global-search',
                            component: GlobalSearchPage
                        },
                        {
                            name: 'circuits',
                            path: 'circuits',
                            component: CircuitsPage
                        },
                        {
                            name: 'circuitsAdd',
                            path: 'circuits/add',
                            component: CircuitsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'circuitsEdit',
                            path: 'circuits/:id/edit',
                            component: CircuitsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'locations',
                            path: 'locations',
                            component: LocationsPage
                        },
                        {
                            name: 'locationsAdd',
                            path: 'locations/add',
                            component: LocationsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'locationsEdit',
                            path: 'locations/:id/edit',
                            component: LocationsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'devices',
                            path: 'devices',
                            component: DevicesPage
                        },
                        {
                            name: 'devicesAdd',
                            path: 'devices/add',
                            component: DevicesAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'devicesEdit',
                            path: 'devices/:subId/edit',
                            component: DevicesAddEditPage,
                            props: { pageType: 'edit' }
                        }
                    ]
                },
                {
                    path: 'vbill',
                    component: EmptyComponent,
                    children: [

                    ]
                },
                {
                    path: 'admin',
                    component: EmptyComponent,
                    children: [
                        {
                            name: 'carriers',
                            path: 'carriers',
                            component: CarriersPage
                        },
                        {
                            name: 'carriersAdd',
                            path: 'carriers/add',
                            component: CarriersAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'carriersEdit',
                            path: 'carriers/:id/edit',
                            component: CarriersAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'circuitTypes',
                            path: 'circuit_types',
                            component: CircuitTypesPage
                        },
                        {
                            name: 'circuitTypesAdd',
                            path: 'circuit_types/add',
                            component: CircuitTypesAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'circuitTypesEdit',
                            path: 'circuit_types/:id/edit',
                            component: CircuitTypesAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'circuitSubtypesAdd',
                            path: 'circuit_types/:id/circuit_subtypes/add',
                            component: CircuitSubtypesAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'circuitSubtypesEdit',
                            path: 'circuit_types/:id/circuit_subtypes/:subId/edit',
                            component: CircuitSubtypesAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'deviceManufacturers',
                            path: 'device_manufacturers',
                            component: DeviceManufacturersPage
                        },
                        {
                            name: 'deviceManufacturersAdd',
                            path: 'device_manufacturers/add',
                            component: DeviceManufacturersAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'deviceManufacturersEdit',
                            path: 'device_manufacturers/:id/edit',
                            component: DeviceManufacturersAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'deviceModels',
                            path: 'device_models',
                            component: DeviceModelsPage
                        },
                        {
                            name: 'deviceModelsAdd',
                            path: 'device_models/add',
                            component: DeviceModelsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'deviceModelsEdit',
                            path: 'device_models/:subId/edit',
                            component: DeviceModelsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'supportContracts',
                            path: 'support_contracts',
                            component: SupportContractsPage
                        },
                        {
                            name: 'supportContractsAdd',
                            path: 'support_contracts/add',
                            component: SupportContractsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'supportContractsEdit',
                            path: 'support_contracts/:id/edit',
                            component: SupportContractsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'supportOrganizations',
                            path: 'support_organizations',
                            component: SupportOrganizationsPage
                        },
                        {
                            name: 'supportOrganizationsAdd',
                            path: 'support_organizations/add',
                            component: SupportOrganizationsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'supportOrganizationsEdit',
                            path: 'support_organizations/:id/edit',
                            component: SupportOrganizationsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'clients',
                            path: 'clients',
                            component: ClientsPage
                        },
                        {
                            name: 'clientsAdd',
                            path: 'clients/add',
                            component: ClientsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'clientsEdit',
                            path: 'clients/:id/edit',
                            component: ClientsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'phoneRangesAdd',
                            path: 'clients/:id/phone_ranges/add',
                            component: PhoneRangesAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'phoneRangesEdit',
                            path: 'clients/:id/phone_ranges/:subId/edit',
                            component: PhoneRangesAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'ratesAdd',
                            path: 'clients/:id/rates/add',
                            component: RatesAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'ratesEdit',
                            path: 'clients/:id/rates/:subId/edit',
                            component: RatesAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'partners',
                            path: 'partners',
                            component: PartnersPage
                        },
                        {
                            name: 'partnersAdd',
                            path: 'partners/add',
                            component: PartnersAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'partnersEdit',
                            path: 'partners/:id/edit',
                            component: PartnersAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'discountsAdd',
                            path: 'partners/:id/discounts/add',
                            component: DiscountsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'discountsEdit',
                            path: 'partners/:id/discounts/:subId/edit',
                            component: DiscountsAddEditPage,
                            props: { pageType: 'edit' }
                        },
                        {
                            name: 'circuitSpeeds',
                            path: 'circuit_speeds',
                            component: CircuitSpeedsPage
                        },
                        {
                            name: 'circuitSpeedsAdd',
                            path: 'circuit_speeds/add',
                            component: CircuitSpeedsAddEditPage,
                            props: { pageType: 'add' }
                        },
                        {
                            name: 'circuitSpeedsEdit',
                            path: 'circuit_speeds/:id/edit',
                            component: CircuitSpeedsAddEditPage,
                            props: { pageType: 'edit' }
                        }
                    ]
                },
                {
                    path: 'change_password',
                    name: 'changePassword',
                    component: ChangePasswordPage
                }
            ]
        },
        {
            name: 'login',
            path: '/login',
            component: Login,
            beforeEnter: ifNotAuthenticated
        }

    ]
});
