<template>
    <b-form class="main-container">
        <h4>Links</h4>
        <b-row class="mb-4">
            <b-col md="5">
                <div class="link-point">
                    <span>Start point </span>
                    <i class="fa fa-map-marker"></i>
                </div>
                <b-form-group label="Location-A" label-for="selectLocationA">
                    <b-select
                        v-model="$v.form[0].location.$model"
                        id="selectLocationA"
                        :options="locations"
                        :class="{ 'is-invalid': $v.form[0].location.$error,
                            'select-removable': $v.form[0].location.$model}"
                        @change="onLocationChange(0)"
                    ></b-select>
                    <b-button
                        v-if="$v.form[0].location.$model"
                        @click="clearInput(0, 'location')"
                        variant="danger"
                        class="fa fa-remove remove-button d-inline-block d-md-none"
                    ></b-button>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <transition name="fade">
                    <b-form-group label="Device" label-for="selectDeviceA"
                                  v-if="$v.form[0].location.$model && devices[0] && devices[0].length">
                        <b-select
                            v-model="$v.form[0].device.$model"
                            id="selectDeviceA"
                            :options="devices[0]"
                            :class="{ 'is-invalid': $v.form[0].device.$error,
                                'select-removable': $v.form[0].device.$model}"
                        ></b-select>
                        <b-button
                            v-if="$v.form[0].device.$model"
                            @click="clearInput(0, 'device')"
                            variant="danger"
                            class="fa fa-remove remove-button d-inline-block d-md-none"
                        ></b-button>
                        <b-form-invalid-feedback>
                            This field is required
                        </b-form-invalid-feedback>
                    </b-form-group>
                </transition>
            </b-col>
            <b-col md="2" class="arrows-col d-none d-md-block">
                <svg style="position: absolute" width="100%" height="130px" xmlns="http://www.w3.org/2000/svg">
                    <line v-show="showLine('location', 'location')"
                          class="animated-line" x1="15%" y1="28px" x2="85%" y2="28px" />
                    <line v-show="showLine('device', 'device')"
                          class="animated-line" x1="15%" y1="113px" x2="85%" y2="113px" />
                    <line v-show="showLine('location', 'device')"
                          class="animated-line" x1="15%" y1="32px" x2="85%" y2="109px" />
                    <line v-show="showLine('device', 'location')"
                          class="animated-line" x1="15%" y1="109px" x2="85%" y2="32px" />
                </svg>
                <div width="100%" height="130px" style="position: relative; width: 100%; height: 130px">
                    <div class="circle fa fa-remove"
                         style="left: 1%; top: 16px;"
                         @click="clearInput(0, 'location')"
                         :class="{ 'active': showLine('location', 'location') || showLine('location', 'device') }"
                         v-show="$v.form[0].location.$model && !$v.form[0].device.$model"/>
                    <div class="circle fa fa-remove"
                         style="left: 86%; top: 16px;"
                         @click="clearInput(1, 'location')"
                         :class="{ 'active': showLine('location', 'location') || showLine('device', 'location') }"
                         v-show="$v.form[1].location.$model && !$v.form[1].device.$model" cx="92%" cy="28px" r="7%"/>
                    <div class="circle fa fa-remove"
                         style="left: 1%; top: 101px;"
                         @click="clearInput(0, 'device')"
                         :class="{ 'active': showLine('device', 'device') || showLine('device', 'location') }"
                         v-show="$v.form[0].device.$model" cx="8%" cy="113px" r="7%"/>
                    <div class="circle fa fa-remove"
                         style="left: 86%; top: 101px;"
                         @click="clearInput(1, 'device')"
                         :class="{ 'active': showLine('device', 'device') || showLine('location', 'device') }"
                         v-show="$v.form[1].device.$model" cx="92%" cy="113px" r="7%"/>
                </div>
            </b-col>

            <b-col md="5">
                <div class="link-point">
                    <span>End point </span>
                    <i class="fa fa-map-marker"></i>
                </div>
                <b-form-group label="Location-B" label-for="selectLocationB">
                    <b-select
                        v-model="$v.form[1].location.$model"
                        id="selectLocationB"
                        :options="locations"
                        :class="{ 'is-invalid': $v.form[1].location.$error,
                            'select-removable': $v.form[1].location.$model }"
                        @change="onLocationChange(1)"
                    ></b-select>
                    <b-button
                        v-if="$v.form[1].location.$model"
                        @click="clearInput(1, 'location')"
                        variant="danger"
                        class="fa fa-remove remove-button d-inline-block d-md-none"
                    ></b-button>
                    <b-form-invalid-feedback>
                        This field is required
                    </b-form-invalid-feedback>
                </b-form-group>
                <transition name="fade">
                    <b-form-group label="Device" label-for="selectDeviceB"
                                  v-if="$v.form[1].location.$model && devices[1] && devices[1].length">
                        <b-select
                            v-model="$v.form[1].device.$model"
                            id="selectDeviceB"
                            :options="devices[1]"
                            :class="{ 'is-invalid': $v.form[1].device.$error,
                                'select-removable': $v.form[1].device.$model }"
                        ></b-select>
                        <b-button
                            v-if="$v.form[1].device.$model"
                            @click="clearInput(1, 'device')"
                            variant="danger"
                            class="fa fa-remove remove-button d-inline-block d-md-none"
                        ></b-button>
                        <b-form-invalid-feedback>
                            This field is required
                        </b-form-invalid-feedback>
                    </b-form-group>
                </transition>
            </b-col>
        </b-row>
    </b-form>
</template>

<script>
import Backend from '../services/backend';
import Preloader from './Preloader';
import StringMask from 'string-mask';
const backend = new Backend();

export default {
    name: 'CircuitLinksForm',
    components: {
        Preloader
    },
    watch: {
        '$route.params': 'handlerForRouteChange'
    },
    data() {
        return {
            form: {},
            title: '',
            locations: [],
            devices: [
                [], []
            ],
            oldLinks: [],
            isFetching: false,
            isSaving: false,
            errorFetching: false,
            successMessage: null,
            errorMessage: null
        };
    },
    created() {
        this.handlerForRouteChange();
    },
    props: [
        'pageType',
        'id',
        'submit'
    ],
    validations: {
        form: {
            0: {
                location: { },
                device: {}
            },
            1: {
                location: {},
                device: {}
            }
        }
    },
    methods: {
        handlerForRouteChange() {
            this.clearForm();
            if (this._props.pageType === 'edit') {
                this.isFetching = true;
                let id = this.$route.params.id;
                backend.getRequest('services/circuit_links/?circuit=' + id)
                    .then((response) => {
                        let links = response.data.results;
                        this.oldLinks = response.data.results;
                        if (links.length === 0) {
                            this.clearForm();
                            return;
                        }
                        this.form[0].device = links[0].device;
                        this.form[0].location = links[0].location;
                        this.loadDevices(0);

                        if (links[1]) {
                            this.form[1].device = links[1].device;
                            this.form[1].location = links[1].location;
                            this.loadDevices(1);
                        }
                    })
                    .catch(this.onFetchingError);
            }
            if (this._props.pageType === 'add') {
                this.clearForm();
                this.errorMessage = null;
                this.successMessage = null;
            }
            this.loadLocations();
        },
        onSubmit(circuitId) {
            let link1 = {
                circuit: circuitId,
                location: this.form[0].location,
                device: this.form[0].device
            };
            let link2 = {
                circuit: circuitId,
                location: this.form[1].location,
                device: this.form[1].device
            };
            this.isSaving = true;
            if (this._props.pageType === 'add') {
                let requests = [];
                if (link1) {
                    requests.push(backend.postRequest('services/circuit_links/', link1));
                }
                if (link2) {
                    requests.push(backend.postRequest('services/circuit_links/', link2));
                }
                this.saveLinks(requests);
            } else if (this._props.pageType === 'edit') {
                let requests = [];
                if (this.oldLinks[0] && !link1.location) {
                    requests.push(backend.deleteRequest('services/circuit_links/' + this.oldLinks[0].id));
                } else if (this.oldLinks[0] && link1.location) {
                    requests.push(backend.putRequest('services/circuit_links/' + this.oldLinks[0].id, link1));
                } else if (!this.oldLinks[0] && link1.location) {
                    requests.push(backend.postRequest('services/circuit_links/', link1));
                }
                if (this.oldLinks[1] && !link2.location) {
                    requests.push(backend.deleteRequest('services/circuit_links/' + this.oldLinks[1].id));
                } else if (this.oldLinks[1] && link2.location) {
                    requests.push(backend.putRequest('services/circuit_links/' + this.oldLinks[1].id, link2));
                } else if (!this.oldLinks[1] && link2.location) {
                    requests.push(backend.postRequest('services/circuit_links/', link2));
                }
                this.saveLinks(requests);
            }
        },
        saveLinks(requests) {
            Promise.all(requests)
                .then(() => {
                    this.errorMessage = null;
                    this.$store.dispatch('addNotification', {
                        text: `Circuit links were saved successfully`,
                        variant: 'success'
                    });
                    this.isSaving = false;
                    if (this.pageType === 'add') {
                        this.clearForm();
                    }
                })
                .catch((error) => {
                    this.$store.dispatch('addNotification', {
                        text: error && error.response && error.response.data ||
                        `Something went wrong with links saving`,
                        variant: 'danger'
                    });
                });
        },
        loadLocations() {
            backend.getRequest('services/locations/')
                .then((response) => {
                    let formatter = new StringMask('$L000000', { reverse: true });
                    this.locations = response.data.results.map((item) => {
                        return {
                            value: item.id,
                            text: formatter.apply(item.number).toUpperCase() + ' | ' + item.name
                        };
                    });
                })
                .catch(this.onFetchingError);
        },
        loadDevices(pointId) {
            let locationId = this.form[pointId].location;
            if (!locationId) {
                return;
            }
            backend.getRequest('services/devices/?location=' + locationId)
                .then((response) => {
                    let formatter = new StringMask('D000000', { reverse: true });
                    this.devices[pointId] = response.data.results.map((item) => {
                        return {
                            value: item.id,
                            text: formatter.apply(item.number) + ' | ' + item.device_model + ' | ' + item.serial_number
                        };
                    });
                    this.devices = [].concat(this.devices);
                })
                .catch(this.onFetchingError);
        },
        onLocationChange(pointId) {
            setTimeout(() => {
                this.loadDevices(pointId);
                this.form[pointId].device = null;
            }, 100);
        },
        onFetchingError(error) {
            this.errorFetching = true;
            this.isFetching = false;
            this.errorMessage = error.response && error.response.data &&
                JSON.stringify(error.response.data).replace(/[[\]{}]+/g, '') ||
                'Something went wrong. Please try again later';
        },
        showLine(type1, type2) {
            if (type1 === 'location' && type2 === 'location') {
                return this.$v.form[0].location.$model && !this.$v.form[0].device.$model &&
                    this.$v.form[1].location.$model && !this.$v.form[1].device.$model;
            } else if (type1 === 'device' && type2 === 'device') {
                return this.$v.form[0].device.$model && this.$v.form[1].device.$model;
            } else if (type1 === 'device' && type2 === 'location') {
                return this.$v.form[1].location.$model && !this.$v.form[1].device.$model &&
                    this.$v.form[0].device.$model;
            } else if (type1 === 'location' && type2 === 'device') {
                return this.$v.form[0].location.$model && !this.$v.form[0].device.$model &&
                    this.$v.form[1].device.$model;
            }
        },
        clearInput(pointId, type) {
            this.form[pointId][type] = null;
        },
        clearForm() {
            this.form = {
                0: {
                    location: null,
                    device: null
                },
                1: {
                    location: null,
                    device: null
                }
            };
            setTimeout(() => this.$v.$reset(), 0);
        }
    }
};
</script>

<style scoped>
    .main-container {
        text-align: left;
    }
    .link-point {
        font-size: 17px;
        font-weight: 700;
        margin-bottom: 10px;
    }
    .link-point span {
        text-transform: uppercase;
    }
    .link-point i {
        font-size: 24px;
        color: #28a745;
    }

    .remove-button {
        width: 40px;
        height: 38px;
    }

    .arrows-col {
        padding-top: 60px;
        padding-left: 0;
        padding-right: 0;
    }
    .arrows-col line {
        stroke: #28a745;
        stroke-width: 2px;
        stroke-linecap: round;
    }

    .arrows-col .animated-line {
        stroke-dasharray: 3, 7;
        stroke-dashoffset: 40px;
        animation: lineMove 2s infinite linear;
    }

    .arrows-col .circle {
        display: inline-block;
        position: absolute;
        width: 15%;
        padding-top: 14%;
        border-radius: 50%;
        border: 1px solid #ced4da;
    }
    .arrows-col .circle:before {
        position: absolute;
        visibility: hidden;
        top: calc(50% - 11px);
        left: calc(50% - 8px);
        font-size: 20px;
        color: #dc3545;
    }
    .arrows-col .circle.active {
        border-color: #28a745;
    }
    .arrows-col .circle:hover {
        cursor: pointer;
        border-color: #dc3545;
    }
    .arrows-col .circle.active:hover:before {
        visibility: visible;
    }

    @keyframes lineMove {
        to {
            stroke-dashoffset: 0;
        }
    }

    @media screen and (max-width: 767px) {
        .select-removable {
            width: calc(100% - 45px);
            margin-right: 5px;
        }
    }
</style>
